import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import {NgForm} from "@angular/forms";
import {ServiceProvider} from "../../providers/service/service";

/**
 * Generated class for the CreatenotificationPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-createnotification',
  templateUrl: 'createnotification.html',
})
export class CreatenotificationPage {

  constructor(public navCtrl: NavController, public navParams: NavParams,private service:ServiceProvider) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad CreatenotificationPage');
  }

  createNotification(temp:NgForm){
    this.service.createNotification(temp.value["title"],temp.value["time"],temp.value["sound"],temp.value["type"])
    this.navCtrl.pop();
  }

}



