import { Component } from '@angular/core';
import {IonicPage, LoadingController, NavController, NavParams} from 'ionic-angular';
import {ServiceProvider} from "../../providers/service/service";
import {CreateevolutionPage} from "../createevolution/createevolution";

/**
 * Generated class for the DevelopmentPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-development',
  templateUrl: 'development.html',
})
export class DevelopmentPage {

  evolution:any = [];
  loading:any;

  constructor(public navCtrl: NavController, public navParams: NavParams,private service:ServiceProvider, private loadingCtrl:LoadingController) {
    this.loading = loadingCtrl.create({content: 'Loading please wait...'});
    service.evolution().subscribe(res => {
      if (res != null && res != undefined ) this.evolution = res
    })
  }
  ionViewDidLoad() {
    console.log('ionViewDidLoad DevelopmentPage');
    this.loading.dismiss();
  }

  viewMore(key) {
    console.log("view MoreKey",key)
  }

  GotoCreate(){
    this.navCtrl.push(CreateevolutionPage);
  }

}



