import { NgModule, ErrorHandler } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { MyApp } from './app.component';

import { HomePage } from '../pages/home/home';
import { TabsPage } from '../pages/tabs/tabs';
import {VideosPage} from "../pages/videos/videos";
import {DevelopmentPage} from "../pages/development/development";
import {TalesPage} from "../pages/tales/tales";
import {NotificationPage} from "../pages/notification/notification";
import {NamesPage} from "../pages/names/names";
import {KickingPage} from "../pages/kicking/kicking";
import {ProfilePage} from "../pages/profile/profile";

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { DocumentViewer } from '@ionic-native/document-viewer';
import { AngularFireModule } from '@angular/fire';
import { AngularFireDatabaseModule } from '@angular/fire/database';
import { AngularFireAuthModule } from '@angular/fire/auth';
import { ServiceProvider } from '../providers/service/service';
import { AngularFireStorageModule } from "@angular/fire/storage";
import { FirebaseConfig } from  "./FriebaseConfig";
import {FormsModule} from "@angular/forms";
import {AuthPage} from "../pages/auth/auth";
import {SignupPage} from "../pages/signup/signup";
import {IonicStorageModule} from "@ionic/storage";
import {CreateevolutionPage} from "../pages/createevolution/createevolution";
import {CreatenotificationPage} from "../pages/createnotification/createnotification";
import {CreaterecordkickingPage} from "../pages/createrecordkicking/createrecordkicking";
import {CreatetalesPage} from "../pages/createtales/createtales";
import {CreatevideoPage} from "../pages/createvideo/createvideo";
import {PortalauthPage} from "../pages/portalauth/portalauth";
import {HttpClientModule} from "@angular/common/http";

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    TabsPage,
    ProfilePage,
    VideosPage,
    DevelopmentPage,
    TalesPage,
    NotificationPage,
    NamesPage,
    KickingPage,
    AuthPage,
    SignupPage,
    CreateevolutionPage,
    CreatenotificationPage,
    CreaterecordkickingPage,
    CreatetalesPage,
    CreatevideoPage,
    PortalauthPage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp),
    AngularFireModule.initializeApp(FirebaseConfig),
    AngularFireDatabaseModule,
    AngularFireAuthModule,
    AngularFireStorageModule,
    FormsModule,
    IonicStorageModule.forRoot(),
    HttpClientModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    TabsPage,
    ProfilePage,
    VideosPage,
    DevelopmentPage,
    TalesPage,
    NotificationPage,
    NamesPage,
    KickingPage,
    AuthPage,
    SignupPage,
    CreateevolutionPage,
    CreatenotificationPage,
    CreaterecordkickingPage,
    CreatetalesPage,
    CreatevideoPage,
    PortalauthPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    DocumentViewer,
    ServiceProvider
  ]
})
export class AppModule {}



