import { Component } from '@angular/core';
import {IonicPage, LoadingController, NavController, NavParams} from 'ionic-angular';
import {ServiceProvider} from "../../providers/service/service";
import {CreatenotificationPage} from "../createnotification/createnotification";

/**
 * Generated class for the NotificationPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-notification',
  templateUrl: 'notification.html',
})
export class NotificationPage {

  CurTime = new Clock();
  notifications:any = [];
  loading:any;

  constructor(public navCtrl: NavController, public navParams: NavParams,private service:ServiceProvider, private loadingCtrl:LoadingController) {
    this.loading = loadingCtrl.create({content: 'Loading please wait...'});
    service.notification().subscribe(res => {
      if (res != null && res != undefined ) this.notifications = res
    })
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad NotificationPage');
    this.CurTime.startClockHandler();
    this.loading.dismiss();
  }

  GotoCreate(){
    this.navCtrl.push(CreatenotificationPage);
  }

}

class Clock {
  time: string = "00:00:00";
  clockHandler: number;

  getTime() {
    var date = new Date();
    return [date.getHours(), date.getMinutes(), date.getSeconds()]
      .map(current => current >= 10 ? current : "0" + current).join(":");
  }

  startClockHandler(): void {
    this.clockHandler = setInterval(function (parent) {
      this.time = this.getTime();
    }.bind(this), 1000);
  }
}



