import {Component} from '@angular/core';
import {IonicPage, LoadingController, NavController, NavParams} from 'ionic-angular';
import {ServiceProvider} from "../../providers/service/service";
import {CreatevideoPage} from "../createvideo/createvideo";
import {DomSanitizer} from "@angular/platform-browser";

/**
 * Generated class for the VideosPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-videos',
  templateUrl: 'videos.html',
})
export class VideosPage {

  videos:any = [];
  loading:any;

  constructor(public navCtrl: NavController, public navParams: NavParams,private service:ServiceProvider, private loadingCtrl:LoadingController ,public sanitizer:DomSanitizer) {
    this.loading = loadingCtrl.create({content: 'Loading please wait...'});
    service.video().subscribe(res => {
      if (res != null && res != undefined ) this.videos = res
    })
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad VideosPage');
    this.loading.dismiss();
  }

  GotoCreate(){
    this.navCtrl.push(CreatevideoPage);
  }

}



