import {Component} from '@angular/core';
import {IonicPage, LoadingController, NavController, NavParams} from 'ionic-angular';
import {ServiceProvider} from "../../providers/service/service";
import {NgForm} from "@angular/forms";

/**
 * Generated class for the NamesPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-names',
  templateUrl: 'names.html',
})
export class NamesPage {

  names: any = [];
  searchName: any = [];
  loading: any;
  tab: string = "name";

  constructor(public navCtrl: NavController, public navParams: NavParams, private service: ServiceProvider, private loadingCtrl: LoadingController) {
    this.loading = loadingCtrl.create({content: 'Loading please wait...'});
    service.names().subscribe(res => {
      if (res != null && res != undefined) this.names = res
    })
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad NamesPage');
    this.loading.dismiss();
  }

  coCancel() {
    this.searchName = this.names;
    this.tab = "name"
  }

  onClickSearch() {
    this.tab = "search"
  }

  onSearch(ev: any) {
    this.searchName = this.names;
    let val = ev.target.value;

    if (val && val.trim() !== '') {
      this.searchName = this.names.filter((item) => {
        return item.DisplayName.toLowerCase().indexOf(val.toLowerCase()) > -1;
      });
    }
  }

  getNames(data: NgForm) {
    this.service.getNames({
      name: data.value['name'],
      gender: data.value['gender'],
      day: data.value['day'],
      mean: data.value['mean']
    }).subscribe(
      (res) => {
        console.log(res);
      }
    );
  }
}



