import { Component } from '@angular/core';
import {IonicPage, LoadingController, NavController, NavParams} from 'ionic-angular';
import {DocumentViewer, DocumentViewerOptions} from "@ionic-native/document-viewer";
import {ServiceProvider} from "../../providers/service/service";
import {CreatetalesPage} from "../createtales/createtales";

/**
 * Generated class for the TalesPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-tales',
  templateUrl: 'tales.html',
})
export class TalesPage {

  tales:any = [];
  loading:any;

  constructor(public navCtrl: NavController, public navParams: NavParams, private document:DocumentViewer ,private service:ServiceProvider, private loadingCtrl:LoadingController) {
    this.loading = loadingCtrl.create({content: 'Loading please wait...'});
    service.tales().subscribe(res => {
      if (res != null && res != undefined ) this.tales = res
    })
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad TalesPage');
    this.loading.dismiss();
  }

  openPdf(title,url){
    const options: DocumentViewerOptions = {
      title: title.toString()
    };
    this.document.viewDocument(url.toString(), 'application/pdf', options);
    console.log('test');
  }

  GotoCreate(){
    this.navCtrl.push(CreatetalesPage);
  }

}



