import { Injectable } from '@angular/core';
import {AngularFireAuth} from "@angular/fire/auth";
import {AngularFireDatabase} from "@angular/fire/database";
import {AngularFireStorage} from "@angular/fire/storage";
import {Storage} from "@ionic/storage";
import {map} from "rxjs/operators";
import {el} from "@angular/platform-browser/testing/src/browser_util";
import {HttpClient, HttpHeaders} from "@angular/common/http";

/*
  Generated class for the ServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class ServiceProvider {

  constructor(private fbAuth:AngularFireAuth, private db:AngularFireDatabase , private fbstore:AngularFireStorage, private storeage:Storage,private http:HttpClient) {
    console.log('Hello ServiceProvider Provider');
  }

  UpdateUserData(){
    this.db.object("users/"+ this.fbAuth.auth.currentUser.uid + "/profile/" ).valueChanges().subscribe(
      value => {
        if(value == null || value == undefined){
          this.db.database.ref("users/"+ this.fbAuth.auth.currentUser.uid + "/profile/").update({
            displayName:this.fbAuth.auth.currentUser.displayName,
            photoURL:this.fbAuth.auth.currentUser.photoURL,
            email:this.fbAuth.auth.currentUser.email,
            phoneNumber:this.fbAuth.auth.currentUser.phoneNumber
          });
          this.UpdateUserData();
        }
        else
        {
          this.storeage.set("UserData",value);
        }
      }
    )
  }

  evolution(){
    return this.db.list('system/evolution').snapshotChanges().pipe(
      map(
        changes => { return changes.map(item => {
          const data = item.payload.exportVal();
          data.id = item.key;
          return data;
        });
        }
      ));
  }

  createEvolution(week,detail){
    this.db.database.ref('system/evolution').push(
      {
        week:week,
        detail:detail
      }
    ).then(val => console.log(val))
  }

  notification(){
    return this.db.list('users/' + this.fbAuth.auth.currentUser.uid + '/notification').snapshotChanges().pipe(
      map(
        changes => { return changes.map(item => {
          const data = item.payload.exportVal();
          data.id = item.key;
          return data;
        });
        }
      ));
  }

  createNotification(title,time,sound,type){
    this.db.database.ref('users/' + this.fbAuth.auth.currentUser.uid + '/notification').push(
      {
        title:title,
        time:time,
        sound:sound,
        type:type
      }
    )
  }

  tales(){
    return this.db.list('system/tales').snapshotChanges().pipe(
      map(
        changes => { return changes.map(item => {
          const data = item.payload.exportVal();
          data.id = item.key;
          return data;
        });
        }
      ));
  }

  createTales(photoURL,file,title,description){
    this.db.database.ref('system/tales').push(
      {
        title:title,
        description:description,
        file:file,
        photoURL:photoURL,
        timeStamp: new Date()
      }
    )
  }

  video(){
    return this.db.list('system/videos').snapshotChanges().pipe(
      map(
        changes => { return changes.map(item => {
          const data = item.payload.exportVal();
          data.id = item.key;
          return data;
        });
        }
      ));
  }

  getUrlVars(url)
  {
    var vars = [], hash;
    var hashes = url.slice(url.indexOf('?') + 1).split('&');
    for(var i = 0; i < hashes.length; i++)
    {
      hash = hashes[i].split('=');
      vars.push(hash[0]);
      vars[hash[0]] = hash[1];
    }
    return vars;
  }

  createVideo(url,title,description){
    if(this.getUrlVars(url)["v"]){
      this.db.database.ref('system/videos').push(
        {
          title:title,
          description:description,
          vid:this.getUrlVars(url)["v"],
          liked:0,
          unliked:0,
          timeStamp:new Date()
        }
      );
      return true;
    }
    else
      return false;
  }

  names(){
    return this.db.list('system/tales').snapshotChanges().pipe(
      map(
        changes => { return changes.map(item => {
          const data = item.payload.exportVal();
          data.id = item.key;
          return data;
        });
        }
      ));
  }

  kickingRecord(){
    return this.db.list('users/' + this.fbAuth.auth.currentUser.uid + '/kickingRecord').snapshotChanges().pipe(
      map(
        changes => { return changes.map(item => {
          const data = item.payload.exportVal();
          data.id = item.key;
          return data;
        });
        }
      ));
  }

  CreateRecordKicking(times){
    this.db.database.ref('users/' + this.fbAuth.auth.currentUser.uid + '/kickingRecord').push(
      {
        kickingTimes:times,
        timeStamp:new Date()
      }
    )
  }

  getNames(data){
    console.log(data);
    var headers = new HttpHeaders();
    headers.append('Access-Control-Allow-Origin' , 'https://www.amarinbabyandkids.com');
    headers.append('origin','https://www.amarinbabyandkids.com');
    headers.append('Access-Control-Allow-Methods', 'POST');
    headers.append('Accept','application/json');
    headers.append('content-type','application/json');
    return this.http.post("https://www.amarinbabyandkids.com/app/themes/talingchan/refreshTable.php",data);
  }
}



