import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import {NgForm} from "@angular/forms";
import {ServiceProvider} from "../../providers/service/service";

/**
 * Generated class for the CreateevolutionPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-createevolution',
  templateUrl: 'createevolution.html',
})
export class CreateevolutionPage {

  constructor(public navCtrl: NavController, public navParams: NavParams,private service:ServiceProvider) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad CreateevolutionPage');
  }

  createEvolution(temp:NgForm) {
    this.service.createEvolution(temp.value["week"], temp.value["detail"]);
    this.navCtrl.pop();
  }

}



