import { Component } from '@angular/core';
import {IonicPage, LoadingController, NavController, NavParams} from 'ionic-angular';
import {ServiceProvider} from "../../providers/service/service";

/**
 * Generated class for the KickingPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-kicking',
  templateUrl: 'kicking.html',
})
export class KickingPage {

  kickingRecord:any = [];
  loading:any;

  constructor(public navCtrl: NavController, public navParams: NavParams,private service:ServiceProvider, private loadingCtrl:LoadingController) {
    this.loading = loadingCtrl.create({content: 'Loading please wait...'});
    service.kickingRecord().subscribe(res => {
      if (res != null && res != undefined ) this.kickingRecord = res
    })
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad KickingPage');
    this.loading.dismiss();
  }

}



