import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import {AngularFireAuth} from "@angular/fire/auth";
import {TabsPage} from "../tabs/tabs";
import {NgForm} from "@angular/forms";
import {auth} from "firebase";
import {SignupPage} from "../signup/signup";
import {ServiceProvider} from "../../providers/service/service";

/**
 * Generated class for the AuthPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-auth',
  templateUrl: 'auth.html',
})
export class AuthPage {

  error:string = "";

  constructor(public navCtrl: NavController, public navParams: NavParams, private firebaseAuth:AngularFireAuth, private service:ServiceProvider) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad AuthPage');
  }

  SignIn(data:NgForm){
    console.log( "SignIn Data => " , data.value);
    this.firebaseAuth.auth.signInWithEmailAndPassword(data.value["email"],data.value["password"]).then(
      (success) => {
        console.log(success);
        console.log(this.firebaseAuth.auth.currentUser);
        this.navCtrl.setRoot(TabsPage);
      }).catch(
      (err) => {
        this.error = err;
        console.log(err);
      })
  }

  SignInWithFacebook(){
    console.log("Singing With Facebook");
    this.firebaseAuth.auth.signInWithPopup(new auth.FacebookAuthProvider()).then(
      (success) => {
        console.log(success);
        console.log(this.firebaseAuth.auth.currentUser);
        this.navCtrl.setRoot(TabsPage);
      }).catch(
      (err) => {
        this.error = err;
        console.log(err);
      })
  }

  SignInWithGoogle(){
    console.log("Singing With Google");
    this.firebaseAuth.auth.signInWithPopup(new auth.GoogleAuthProvider).then(
      (success) => {
        console.log(success);
        console.log(this.firebaseAuth.auth.currentUser);
        this.navCtrl.setRoot(TabsPage);
      }).catch(
      (err) => {
        this.error = err;
        console.log(err);
      })
  }

  signup(){
    this.navCtrl.push(SignupPage);
  }
}



