import { Component } from '@angular/core';
import { Platform } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import {TabsPage} from "../pages/tabs/tabs";
import {AngularFireAuth} from "@angular/fire/auth";
import {ServiceProvider} from "../providers/service/service";
import {PortalauthPage} from "../pages/portalauth/portalauth";

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  rootPage:any = PortalauthPage;

  constructor(platform: Platform, statusBar: StatusBar, splashScreen: SplashScreen,private firebaseAuth:AngularFireAuth , private service:ServiceProvider) {
    platform.windowLoad(()=>{
      splashScreen.show();
    });
    platform.ready().then(async () => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      console.log('verifying Authentication...');
      await this.firebaseAuth.authState.subscribe(
        (user) => {
          console.log(user);
          if(user != null){
            this.service.UpdateUserData();
            this.rootPage = TabsPage;
          }
        }
      );
      statusBar.styleDefault();
      splashScreen.hide();
    });
  }
}



