import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import {ServiceProvider} from "../../providers/service/service";
import {NgForm} from "@angular/forms";

/**
 * Generated class for the CreatevideoPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-createvideo',
  templateUrl: 'createvideo.html',
})
export class CreatevideoPage {

  constructor(public navCtrl: NavController, public navParams: NavParams,private service:ServiceProvider) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad CreatevideoPage');
  }

  createVideos(temp:NgForm){
    this.service.createVideo(temp.value["url"],temp.value["title"],temp.value["description"]);
  }

}



