import { Component } from '@angular/core';
import {AlertController, NavController} from 'ionic-angular';
import {DevelopmentPage} from "../development/development";
import {KickingPage} from "../kicking/kicking";
import {NamesPage} from "../names/names";
import {NotificationPage} from "../notification/notification";
import {TalesPage} from "../tales/tales";
import {VideosPage} from "../videos/videos";

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})

export class HomePage {
  DevelopmentPage = DevelopmentPage;
  KickingPage = KickingPage;
  NamesPage = NamesPage;
  NotificationPage = NotificationPage;
  TalesPage = TalesPage;
  VideosPage = VideosPage;
  constructor(public navCtrl: NavController,private alertCtrl:AlertController) {

  }
  ChangeRoot(root){
    this.navCtrl.push(root);
  }

  presentAlertCommingSoon() {
    let alert = this.alertCtrl.create({
      title: 'Alert',
      subTitle: 'Coming Soon',
      buttons: ['Dismiss']
    });
    alert.present();
  }
}



