import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import {ServiceProvider} from "../../providers/service/service";
import {NgForm} from "@angular/forms";

/**
 * Generated class for the CreatetalesPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-createtales',
  templateUrl: 'createtales.html',
})
export class CreatetalesPage {

  constructor(public navCtrl: NavController, public navParams: NavParams,private service:ServiceProvider) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad CreatetalesPage');
  }

  createTales(temp:NgForm){
    this.service.createTales(temp.value["photoURL"],temp.value["file"],temp.value["title"],temp.value["description"])
    this.navCtrl.pop();
  }

}



